    id: 0,
    title: "第一回　纵横钩党清流祸　峭茜风期月旦评",
    content:
      "   北风如刀，满地冰霜。<br>江南近海滨的一条大路上，一队清兵手执刀枪，押着七辆囚车，冲风冒寒，向北而行。<br>前面三辆囚车中分别监禁的是三个男子，都作书生打扮，一个是白发老者，两个是中年人。后面四辆囚车中坐的是女子，最后一辆囚车中是个少妇，怀中抱着个女婴，女婴啼哭不休。她温言相呵，女婴只是大哭。囚车旁一清兵恼了，伸腿在车上踢了一脚，喝道：“再哭，再哭，老子踢死你！”那女婴一惊，哭得更加响了。<br>离开道路数十丈处有座大屋，屋檐下站着一个中年文士，一个十一二岁的小孩。那文士见到这等情景，不禁长叹一声，眼眶也红了，说道：“可怜，可怜！”<br>农小孩问道：“爹爹，他们犯了什么罪？”那文士道：“又犯了什么罪？昨日和今朝已逮去了三十几人，都是我们浙江有名的人，个个都是无辜株连。”他说到“无辜株连”四子，声音压得甚低，生怕给押囚车的官兵听见了。那小孩道：“哪个小还在吃奶，难道也犯了罪么？真没道理。”那文士道：“你懂得官兵没道理，真是好孩子。咳，人为刀俎，我为鱼肉，人为鼎锅，我为麋鹿！”<br>那小孩道：“爹，你前几天教过我。人为刀俎，我为鱼肉，就是给人家斩割屠杀的意思。人家是切菜刀，是铁板，我们就是鱼和肉。“人为鼎锅，我为麋鹿”这两句话，意思也差不多么？”那文士道：“正是！”眼见官兵和囚车已经去远，拉着小孩的手道：“外面风大，我们回屋里去。”当下父子二人走进书房。",
    ctime: "1547195549",
    tags: "鹿鼎记,韦小宝,金庸",
    views: "1245",
    link:'###'
